<template>
  <div>
    <v-card id="honors" class="common-card mb-5">
      <v-card-title class="mb-9 pa-0"
        ><span class="text-gradient mainFont--text">{{ terms.honors }}</span></v-card-title
      >
      <v-card-text class="pa-0">
        <span class="honors">
          <v-row class="pl-0">
            <v-col
              cols="12"
              v-for="(item, index) in honors"
              :key="index"
              class="greyColor--text pt-0 pb-3 d-flex"
            >
              <div class="certificate-img" @click="openImage">
                <img :src="item.image" width="65" height="90" class="rounded" />
                <v-icon class="zoom-icon d-none d-lg-block" color="white">$show</v-icon>
              </div>
              <div
                :class="isLtr ? 'ml-4' : 'ms-4'"
                class="d-flex flex-column justify-space-between"
              >
                <!--Title -->
                <h3
                  class="greyColor--text"
                  :class="lang == 'KO' ? 'font-weight-bold fs-xxlarge' : ''"
                >
                  {{ item[lang + 'title'] }}
                </h3>
                <div>
                  <p
                    class="mb-0 mt-1 grey--text text--darken-1 d-none d-sm-block"
                    :class="!isLtr ? 'persian-font' : ''"
                  >
                    {{ item[lang + 'description'] }}
                  </p>
                  <p
                    class="mb-0 mt-1 grey--text text--darken-1"
                    :class="!isLtr ? 'persian-font' : ''"
                  >
                    {{ item[lang + 'date'] }}
                  </p>
                </div>
              </div>
            </v-col>
          </v-row>
        </span>
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
import utilities from './../mixin/utilities';
export default {
  mixins: [utilities],

  methods: {
    openImage() {
      let btn = document.createElement('a');
      btn.setAttribute('href', '/certificate.jfif');
      btn.setAttribute('target', 'blank');
      btn.click();
    },
  },
};
</script>
